*{
	padding: 0;
	margin: 0;
}

body {background-color: #111;
	color: white;
  padding: 0;
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@-webkit-keyframes index__spin__uB5Zv {
  
  from {
    -webkit-transform: rotate( 0deg );
            transform: rotate( 0deg );
  }
  
  to {
    -webkit-transform: rotate( 360deg );
            transform: rotate( 360deg );
  }
}
@keyframes index__spin__uB5Zv {
  
  from {
    -webkit-transform: rotate( 0deg );
            transform: rotate( 0deg );
  }
  
  to {
    -webkit-transform: rotate( 360deg );
            transform: rotate( 360deg );
  }
}

.index__space__3WTLu {
  position: absolute;
  width: 400vw;
  height: 400vh;
  -webkit-animation: index__spin__uB5Zv 240s linear infinite;
          animation: index__spin__uB5Zv 240s linear infinite;
  background-size: 240px;
  -webkit-backface-visibility: visible;
          backface-visibility: visible;
  
  /* Had to base64 SVG background for FF compatibility */
  background-image: url(data:image/svg+xml;base64,);
}




code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
